
.home .joinUs {
    width: 100%;

    padding-top: 1rem;
    padding-bottom: 1rem;

    background-color: #212529;
}

.home .joinUs .btn_card {
    width: 100%;
    height: 10rem;

    background-color: transparent;
    color: #f0f0f0;

    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;
}
.home .joinUs .btn_card .icon {
    font-size: 3rem;
    font-weight: 500;

    margin-right: 0.5rem;
    margin-bottom: 0.2rem;

    transition: 0.5s;
}
.home .joinUs .btn_card p {
    font-size: 3rem;
    font-weight: 400;

    margin: 0;

    transition: 0.5s;
}

.home .joinUs .btn_card:hover p {
    font-size: 3.1rem;
    transition: 0.2s;
}
.home .joinUs .btn_card:hover .icon {
    font-size: 3.1rem;
    transition: 0.2s;
}

/** PAPERS */
.home .papers {
    width: 100%;
    margin: 2rem 0;
    background-color: #ffffff;
}

.home .papers .text {
    width: 100%;    

}

.home .papers .text .subtitle {
    font-size: 1.5rem;
    font-weight: 400;

    margin: 0;
}

.home .papers .text .title {
    font-size: 3rem;
    font-weight: 500;
    letter-spacing: 0.4rem;

    
}

.home .papers .text .info {
    font-size: 1.2rem;
    font-weight: 300;

}

.home .papers .text .more {
    width: fit-content;
    cursor: pointer;
    color: #ffa400;
}

.home .papers .list {
    width: 100%;

    /* margin-left: 2rem; */
}

.home .papers .list .paperblock {
    width: 100%;    
    height: 8rem;

    display: flex;

    margin-top: 1rem;

    border-radius: 0.5rem;
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.2);

    background-color: #ffffff;
}

.home .papers .list .paperblock > .paperimage {
    width: 8rem;
    height: 8rem;

    border-radius: 0.5rem;
    overflow: hidden;
}

.home .papers .list .paperblock > .papertitle {
    width: Calc(100% - 8rem);

    padding-left: 0.5rem;

    font-size: 1rem;
    font-weight: 300;

    display: flex;
    align-items: center;

}

