/** COVER */
.home .cover {
    position: relative;
    width: 100%;
    height: 100vh;

   
}

.home .cover .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    overflow: hidden;

    background-color: #f0f0f0;

}

.home .cover .background img {       
    min-height: 100%;
    width: 100%;
    position: absolute;

    top: 0px;
    
}

.home .cover .title {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    z-index: 5;

    display: flex;
    flex-direction: column;
    justify-content: center;
}


.home .cover .title .ncku {   

    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;

    margin-left: 8rem;
}

.home .cover .title .ncku img {
    width: 5rem;
}

.home .cover .title .ncku p {
    font-size: 2rem;
    font-weight: 300;
    margin-left: 0.2rem;
    margin-bottom: 0;

    color: #403e3e;
}

.home .cover .title .nclab {   
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    flex-wrap: wrap;


    margin-left: 8rem;
}

.home .cover .title .nclab p {
    font-size: 3.5rem;
    font-weight: 400;

    margin-right: 1rem;

    color: #403e3e;
}
