@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap");

@media screen and (min-width: 601px) {
  html {
    font-size: 18px;
  }
}

@media screen and (max-width: 600px) {
  html {
    font-size: 10px;
  }
}

body {
  font-family: "Noto Sans TC", sans-serif !important;
}

.navbar-outer {
  position: absolute;
  z-index: 20;
  left: 0px;
  top: 0px;

  width: 100%;
  height: 5rem;

  padding: 0.5rem 1rem 0 1rem;

  transition: 0.35s;

  background-color: transparent;
}

.navMove {
  background-color: #ffffff;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);

  position: fixed;

  transition: 0.35s;
}

.navbar-outer .navbar-inner {
  position: relative;

  width: 100%;
  height: 100%;

  background-color: transparent;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.navbar-outer .navbar-inner .navbarBrand {
  width: 60%;
  height: 100%;

  margin-left: 1rem;
}

.navbar-outer .navbar-inner .navbarBrand .navbarBrand-inner {
  display: flex;
  justify-items: start;
  align-items: center;

  width: 100%;
  height: 100%;

  /* color: #f0f0f0; */
  color: #403e3e;
}

.navbar-outer.navMove .navbar-inner .navbarBrand .navbarBrand-inner {
  display: flex;
  justify-items: start;
  align-items: center;

  width: 100%;
  height: 100%;

  color: #403e3e;
}

.navbar-outer .navbar-inner .navbarBrand .brandName {
  font-size: 1.5rem;
  letter-spacing: 0rem;
  font-weight: 400;

  margin: 0;

  opacity: 0;

  transition: 0.1s;
}
.navbar-outer.navMove .navbar-inner .navbarBrand .brandName {
  font-size: 1.5rem;
  letter-spacing: 0rem;
  font-weight: 400;

  margin: 0;

  opacity: 1;

  transition: 0.5s;
}

.navbar-outer .navbar-inner .navbarBrand .brandImg {
  height: 3rem;
  width: 3rem;
}

.navbar-outer .navbar-inner .nav .nav-link {
  /* color: #f0f0f0; */
  color: #403e3e;
}

.navbar-outer.navMove .navbar-inner .nav .nav-link {
  color: #403e3e;
}

/** FOOTER */
.footer {
  width: 100%;
  height: 2rem;

  display: block;

  background-color: #ffffff;
  color: #403e3e;

  margin-top: 1rem;
}

.footer .imgbox {
  width: 100%;
  height: 4rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.footer .imgbox img {
  max-width: 100%;
  max-height: 100%;
}

.footer .copyright {
  width: 100%;
  background-color: transparent;
}

/** PUBLICATION */
.publication {
  position: relative;
  width: 100%;

  background-color: #f0f0f0;

  padding-top: 3rem;
  padding-bottom: 3rem;
}

.publication header {
  background-color: #403e3e;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 2rem;
}

.publication header .title {
  width: 100%;
  padding: 1rem 0;

  font-size: 1.5rem;
  font-weight: 200;

  color: #f0f0f0;

  text-align: center;
}

.publication header .title p {
  margin: 0;
}

.publications-card {

  display: flex;

  margin-top: 1rem;

  width: 100%;
  min-height: 15rem;

  transition: 0.25s;

  margin-bottom: 1rem;

  background-color: transparent;
  color: #403e3e;

  border-left: 2px solid #403e3e;
}

.publications-card:hover {
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
  border-radius: 5px;

  background-color: #d0d0d0;
  color: #403e3e;

  border-color: transparent;

  transition: 0.25s;
}

.publications-card > .image_area {
  width: 15rem;
}

.publications-card > .text_area {
  width: Calc(100% - 15rem);
}

.publications-card > .text_area > .title {
  font-size: 1.8rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  margin-top: 0rem;
  margin-bottom: 0px;

  padding: 0.5rem;
}

.publications-card > .text_area > .author {
  font-size: 0.8rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;

  margin-top: 0rem;
  margin-bottom: 0px;

  padding: 0.5rem;
}
.publications-card > .text_area > .at {
  font-size: 1.2rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;

  margin-top: 0rem;
  margin-bottom: 0px;

  padding: 0.5rem;
}

/** RESEARCH */
.research {
  position: relative;
  width: 100%;

  background-color: #f0f0f0;

  padding-top: 3rem;
  padding-bottom: 3rem;
}

.research .introCard {
  min-height: 20rem;

  display: flex;
  flex-direction: row;
  align-items: center;

  padding: 2rem;
  margin: 1rem 0;

  background-color: #ffffff;
  border-radius: 5px;

  transition: 0.5s;
}
.research .introCard:hover {
  box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.3);

  transition: 0.3s;

  cursor: pointer;
}

.research .introCard .image {
  height: 100%;
  width: 45%;

  margin-left: auto;

  display: flex;
  flex-direction: row;
  justify-content: center;

  overflow: hidden;
  border-radius: 5px;
  background-color: #ffffff;
}

/* .research .introCard .image img {
  height: 100%;
} */

.research .introCard .title {
  width: 50%;

  margin: 1rem;

  font-size: 1.8rem;
  font-weight: 300;
  color: #403e3e;
}

.research .introPage .projectName {
  font-size: 2.5rem;
  font-weight: 300;
  color: #403e3e;

  display: flex;
  justify-content: center;
  align-items: center;

  border-bottom: 1.5px solid #403e3e;
}

.research .introPage .description {
}

.research .introPage .description .title {
  font-size: 1.8rem;
  font-weight: 300;
  color: #403e3e;

  margin-top: 1rem;
}

.research .introPage .description .text {
  font-size: 1.2rem;
  font-weight: 300;
  color: #403e3e;

  padding-left: 0.6rem;
  margin-left: 0.4rem;

  border-left: 2px solid #5899e2;
}

.research .introPage .image {
  display: flex;
  flex-direction: column;
}

.research .introPage .image .title {
  font-size: 1.8rem;
  font-weight: 300;
  color: #403e3e;

  margin-top: 1rem;
}

.research .introPage .image img {
  max-width: 100%;

  margin: 0 1rem 0 1rem;

  border-radius: 5px;
  background-color: #ffffff;

  overflow: hidden;
}

.research .introPage .image .text {
  font-size: 1.2rem;
  font-weight: 300;
  color: #403e3e;

  text-align: center;

  margin-top: 1rem;
}

/* CONTACT */
.contact {
  position: relative;
  width: 100%;
  background-color: #f0f0f0;

  padding-bottom: 3rem;
  padding-top: 3rem;
}

.contact .title {
  font-size: 1.6rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;
  color: #403e3e;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 100%;
}

.contact .text {
  font-size: 1rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;
  color: #403e3e;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  height: 100%;
}

.contact .text .subtitle {
  font-size: 1.2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;
  color: #403e3e;
}

/* HONOR */
.honor {
  width: 100%;
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #f0f0f0;

  overflow: hidden;
}

.honor .section {
  width: 80%;
  position: relative;
  background-color: #ffffff;

  margin: 2rem 0;

  border-radius: 5px;
  /* box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3); */
}

.honor .section .time-line {
  position: absolute;
  left: -1.2rem;
  top: 6rem;
  bottom: -7rem;

  z-index: 2;

  border-left: 5px solid #403e3e;
}

.honor .section .time-circle {
  position: absolute;
  top: 4rem;
  left: -1.6rem;

  width: 1rem;
  height: 1rem;

  background-color: transparent;

  border-radius: 50%;
  border-color: #403e3e;
  border-width: 5px;
  border-style: solid;
}

.honor .section .year {
  font-size: 2rem;
  font-weight: 300;
}

.honor .section .title {
  font-size: 2rem;
  font-weight: 200;
}

.honor .section .award {
  font-size: 2rem;
  font-weight: 200;
}

/* PEOPLE */
.people {
  width: 100%;

  position: relative;

  background-color: #f0f0f0;

  padding-top: 3rem;
  padding-bottom: 2rem;
}

.people header {
  background-color: #403e3e;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 2rem;
}

.people header .title {
  width: 100%;
  padding: 1rem 0;

  font-size: 1.5rem;
  font-weight: 200;

  color: #f0f0f0;

  text-align: center;
}

.people header .title p {
  margin: 0;
}

.people .section {
  margin-top: 5rem;
  margin-bottom: 2rem;
}

.people .section .title {
  font-size: 2rem;
  font-weight: 300;
  color: #403e3e;

  text-align: center;

  border-bottom: 1.5px solid #403e3e;
}

.people .section .introCard {
  width: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;

  background-color: #ffffff;
  border-radius: 5px;

  overflow: hidden;
}

.people .section .introCard .image {
  width: 50%;
  margin: 0.5rem;

  border-radius: 5px;

  overflow: hidden;
}

.people .section .introCard .text {
  width: 50%;

  display: flex;
  flex-direction: column;

  margin-left: 1rem;
  margin-right: 1rem;
}

.people .section .introCard .text .name {
  font-size: 2rem;
  font-weight: 300;
  color: #403e3e;

  margin: 0;
}

.people .section .introCard .text .description {
  font-size: 1rem;
  font-weight: 300;
  color: #403e3e;

  margin-top: 0.5rem;
  margin-bottom: 0;
}

.people .principalInvestiagtorPage {
  margin-bottom: 2rem;
}

.people .principalInvestiagtorPage .name {
  font-size: 2rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  color: #403e3e;
}

.people .principalInvestiagtorPage .appellation {
  font-size: 1rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;

  margin-bottom: 0.3rem;

  color: #403e3e;
}

.people .principalInvestiagtorPage .title {
  font-size: 1.6rem;
  letter-spacing: default;
  font-weight: 400;
  line-height: normal;

  margin-top: 1rem;

  color: #403e3e;
}

.people .principalInvestiagtorPage .title p {
  margin-bottom: 0.8rem;

  color: #403e3e;
}

.people .principalInvestiagtorPage .text {
  font-size: 1rem;
  letter-spacing: default;
  font-weight: 300;
  line-height: normal;

  margin-left: 1rem;

  color: #403e3e;
}

.people .principalInvestiagtorPage .text p {
  margin-bottom: 0.5rem;

  color: #403e3e;
}

/* component */
/** COVER */
.page .cover {
  position: relative;
  width: 100%;
  height: 100vh;

 
}

.page .cover .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;;
  height: 100%;

  overflow: hidden;

  background-color: #f0f0f0;

}

.page .cover .background img {       
  min-height: 100%;
  width: 100%;
  position: absolute;

  top: 0;
  
}

.page .cover .title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  z-index: 5;

  display: flex;
  flex-direction: column;
  justify-content: center;
}


.page .cover .title .ncku {   

  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;

  margin-left: 8rem;
}

.page .cover .title .ncku img {
  width: 5rem;
}

.page .cover .title .ncku p {
  font-size: 2rem;
  font-weight: 300;
  margin-left: 0.2rem;
  margin-bottom: 0;

  color: #403e3e;
}

.page .cover .title .nclab {   
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;

  width: fit-content;

  margin-left: 8rem;

  border-bottom: 0.1rem solid #403e3e;
}

.page .cover .title .nclab p {
  font-size: 3.5rem;
  font-weight: 400;

  margin-right: 1rem;

  color: #403e3e;
}

.page .cover .title .pagetitle {   
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;


  margin-left: 8rem;
}

.page .cover .title .pagetitle p {
  font-size: 3rem;
  font-weight: 400;

  margin-right: 1rem;

  color: #403e3e;
}


/* NAV */
.background {
  position: absolute;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100vw;

  z-index: 5;
  background: #ffffff;
}

.background .nav {
  height: 100%;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}

.background .nav .nav-link {
  color: #403e3e;

  font-size: 3rem;
}

.toggle {
  position: absolute;
  z-index: 6;

  right: 2rem;
  top: 1.25rem;

  height: 3rem;
  width: 3rem;

  /* background-color: #ffffff;
  border-radius: 1rem; */

  display: flex;
  justify-content: center;
  align-items: center;
}

.toggle button {
  outline: none;
  border: none;
  cursor: pointer;

  border-radius: 50%;
  background: transparent;

  margin: 1px 0 0 1px;
  padding: 0;
}

.toggle button:focus {
  outline: 0;
}

/** PHOTO */
.photo {
  position: relative;
  width: 100%;

  background-color: #f0f0f0;

  padding-top: 3rem;
  padding-bottom: 3rem;
}

.photo .card {
  background-color: #ffffff;

  border-radius: 5px;
  border-width: 0;
}

.photo .card .imagebox-1 {
  position: relative;
  width: 100%;
  height: 0;

  padding-bottom: Calc(100% / 16 * 9);
}

.photo .card .imagebox-1 > .imagebox-2 {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.photo .card .imagebox-1 > .imagebox-2 > .imagebox-3 {
  height: 100%;

  display: flex;
  flex-direction: row;
  justify-content: center;

  border-radius: 5px;

  overflow: hidden;
}

.photo .card .imagebox-1 > .imagebox-2 > .imagebox-3 > img {
  height: Calc(100% - 1rem);

  margin: 0.5rem;

  border-radius: 5px;
}

.photo .card .text {
  font-size: 1.5rem;
  font-weight: 300;
  color: #403e3e;
  text-align: center;
}

/** MANAGE */

.manage {
  width: 100%;
  
  position: relative;
}

.manage .inner {
  display: flex;
  width: 100%;

  padding: 1rem;
}

.manage .inner .table {
  width: 30vw;

 

  margin: 0.5rem;
}

.manage .inner .form {
  width: 65vw;

  margin: 0.5rem;
}

.manage .inner .form .from-area {
  padding: 1rem;

  border-radius: 5px;
  border-color: #403e3e;
  border-style: solid;
  border-width: 1px;
}

.manage .inner .form .from-area > p {
  text-align: center;

  font-size: 1.5rem;
  font-weight: 500;
}

.manage .pageSection {
  width: 100%;

  margin-top: 1rem;
  padding-left: 3rem;
}

.manage .pageSection .title {
  width: 100%;

  font-size: 1rem;
  font-weight: 300;
}

.manage .pageSection .title p {
  margin: 0;
}

.manage .pageSection .pageName {
  width: fit-content;

  cursor: pointer;

  padding-bottom: 1rem;
  margin-bottom: 1rem;

  font-size: 3rem;
  font-weight: 300;
  line-height: 3rem;
}
.manage .pageSection .pageName:hover {
  font-weight: 400;
}

.manage .pageSection .pageName p {
  margin-top: auto;
  margin-bottom: auto;
}

.manage .btnArea {
  display: flex;
}

.pageSelectModal {
  margin: 1rem;
 
}

.pageSelectModal .title {
  font-size: 1rem;
  font-weight: 300;

  width: 100%;

  text-align: center;

  border-bottom: 1px solid #212020;
}

.pageSelectModal .item {
  width: 100%;

  font-size: 2rem;
  font-weight: 300;

  text-align: center;

  cursor: pointer;
}
.pageSelectModal .item:hover {
  font-weight: 400;
}

.manage .onSelect {
  border-radius: 5px;
  border-width: 1.5px;
  border-color: red;
  border-style: solid;
}

/** LOGIN */
.login {
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #212020;
}

.login .form-card {
  background-color: #f0f0f0;
  border-radius: 5px;

  padding: 3rem;
}


/** DRAGLIST */
.dragList {
  height: 100%;
  width: 100%;

  padding: 1rem;

  border-radius: 5px;
  border-color: #403e3e;
  border-style: solid;
  border-width: 1px;

}

.dragList > p {
  text-align: center;

  font-size: 1.5rem;
  font-weight: 500;
}

.dragList .dragCard {
  width: 100%;
  min-height: 1rem;

  margin-bottom: 0.5rem;

  border-radius: 5px;
  border: 1px solid #ced4da;

  cursor: pointer;

}
.dragList .dragCard:hover {
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}

.dragList .dragCard .subtitle {
  font-size: 0.8rem;
  font-weight: 300;

  margin: 0.2rem 0 0 0.2rem;
}

.dragList .dragCard .text {
  font-size: 1rem;
  font-weight: 400;

  margin: 0.5rem 0.5rem;

  text-align: center;
}